	module.exports = {"data":{"markdownRemark":{"html":"<h2>Comments in CSS</h2>\n<p>Comments are used in CSS to explain a block of code or to make temporary changes during development. The commented code doesn’t execute. The comment syntax in CSS works for both single and multi-line comments.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code>    <span class=\"token comment\" spellcheck=\"true\">/*\n        This is\n        a multi-line\n        comment\n    */</span>\n    <span class=\"token comment\" spellcheck=\"true\">/* This is a single line comment*/</span>\n    <span class=\"token selector\">.group:after</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token property\">content</span><span class=\"token punctuation\">:</span> <span class=\"token string\">\"\"</span><span class=\"token punctuation\">;</span>\n        <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> table<span class=\"token punctuation\">;</span>\n        <span class=\"token property\">clear</span><span class=\"token punctuation\">:</span> both<span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>You can add as many comments to your stylesheet as you like. It’s good practice to use CSS comments to help identify parts of any stylesheet that might be difficult to understand from a cursory glance. Comments are especially important when working in a team, when your code must be understood by others. By using CSS comments to make your stylesheets more readable, the CSS will be easier to maintain in the future.</p>\n<h2>Comment Formats</h2>\n<p>Comments should be used everyday in your CSS to keep in maintainable and readable by any dev who dives into said CSS.\nHere are a few exmples to get you started of CSS comments you can use in your daily work to make your life that bit easier.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token comment\" spellcheck=\"true\">/* ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++\n   ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++\n   CSS TABLE OF CONTENTS\n   \n   1.0 - Reset\n   2.0 - Fonts\n   3.0 - Globals\n   4.0 - Color Palette\n   5.0 - Header\n   6.0 - Body\n       6.1 - Sliders\n       6.2 - Imagery\n   7.0 - Footer\n   ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++\n++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++ */</span>\n\n<span class=\"token comment\" spellcheck=\"true\">/****************************************************************************\n1.0 - Reset */</span>\n\n<span class=\"token comment\" spellcheck=\"true\">/****************************************************************************\n2.0 - Fonts */</span>\n\n<span class=\"token comment\" spellcheck=\"true\">/****************************************************************************\n3.0 - Globals */</span>\n\n<span class=\"token comment\" spellcheck=\"true\">/****************************************************************************\n4.0 - Color Palette */</span>\n\n<span class=\"token comment\" spellcheck=\"true\">/****************************************************************************\n5.0 - Header */</span>\n\n<span class=\"token comment\" spellcheck=\"true\">/****************************************************************************\n6.0 - Body */</span>\n\n    <span class=\"token comment\" spellcheck=\"true\">/************************************************************************\n    5.1 - Sliders */</span>\n    \n    <span class=\"token comment\" spellcheck=\"true\">/************************************************************************\n    5.2 - Imagery */</span>\n    \n<span class=\"token comment\" spellcheck=\"true\">/****************************************************************************\n7.0 - Footer */</span>\n``` css\n\n### More <span class=\"token property\">Information</span><span class=\"token punctuation\">:</span>\n\nMDN <span class=\"token property\">documentation</span><span class=\"token punctuation\">:</span> &lt;a href=<span class=\"token string\">'https://developer.mozilla.org/en-US/docs/Web/CSS/Comments'</span> target=<span class=\"token string\">'_blank'</span> rel=<span class=\"token string\">'nofollow'</span>>MDN&lt;/a>\n\nCSS Comments by Adam <span class=\"token property\">Roberts</span><span class=\"token punctuation\">:</span> &lt;a href=<span class=\"token string\">'https://www.sitepoint.com/css-comments/'</span> target=<span class=\"token string\">'_blank'</span> rel=<span class=\"token string\">'nofollow'</span>>Sitepoint&lt;/a>\n</code></pre>\n      </div>","fields":{"slug":"/css/comments-in-css/"},"frontmatter":{"title":"Comments in CSS"}}},"pathContext":{"slug":"/css/comments-in-css/"}}

